export default {
  name: "Ment",
  props: {
    showMent: {
      type: Boolean,
      default: false
    },
    MentList: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      visables: this.showMent,
      list: this.MentList.map(item => {
        item.selected = false;
        return item
      }),
      selectedList: [],
    }
  },
  watch: {
    showMent(n) {
      this.visables = n;
    },
  },
  methods: {
    selected(tokenId) {
      this.list = this.list.map(item => {
        if (item.tokenId == tokenId.tokenId) {
          item.selected = !item.selected;
        }
        return item
      })
      this.selectedList = this.list.filter(item => {
        return item.selected == true
      }).map(item => {
        return item.tokenId
      })
    },
    close() {
      this.$emit("closeMent", false);
    },
    MentToken(tokens) {
      if (tokens.length > 0) {
        this.$emit("closeMent", tokens)
      } else {
        this.$toast.fail("Ment数量不能小于0");
      }
    }
  },
  computed: {
    MATIC() {
      return this.selectedList.length * 100;
    }
  }
}

